appCommonDirectiveModule.filter('parseFieldName', function() {
    return function(input) {
        var value = [];
        for (var i=0;i<input.length;i++){
            value.push(input[i].fieldName);
        }
        var result = value.join(",");
        if(result.length>10){
            result = result.substring(0,10)+"...";
        }
        return result;
    }
});

