appCommonDirectiveModule.directive('tmPagination',['$timeout',function($timeout,$scope,toastr){
    return {
        restrict: 'EA',
        template: '<div class="page-list">' +
            '<ul class="pagination" ng-show="conf.totalItems > 0">' +
            '<li ng-class="{disabled: conf.currentPage == 1}" ng-click="prevIndex()"><span></span></li>' +
            '<li ng-class="{disabled: conf.currentPage == 1}" ng-click="prevPage()"><span></span></li>' +
            '<li ng-repeat="item in pageList track by $index" ng-class="{active: item == conf.currentPage, separate: item == \'...\'}" ' +
            'ng-click="changeCurrentPage(item)">' +
            '<span>{{ item }}</span>' +
            '</li>' +
            '<li ng-class="{disabled: conf.currentPage == conf.numberOfPages}" ng-click="nextPage()"><span></span></li>' +
            '<li ng-class="{disabled: conf.currentPage == conf.numberOfPages}" ng-click="nextBack()"><span></span></li>' +
            '</ul>' +
            '<div class="page-total" ng-show="conf.totalItems > 0">' +
/*
            '<select ng-model="conf.itemsPerPage" ng-options="option for option in conf.perPageOptions " ng-change="changeItemsPerPage()"></select>' +
*/
            '<strong>{{ conf.totalItems }}</strong> ' +
            '<input type="text"  ng-model="jumpPageNum" ng-keyup="jumpPageKeyUp($event)"/>' +
            '</div>' +
            '<div class="no-items" ng-show="conf.totalItems <= 0"></div>' +
            '</div>',
        replace: true,
        scope: {
            conf: '='
        },
        link: function(scope, element, attrs) {

            var conf = scope.conf;
            scope.$watch('conf.totalItems', function(value, oldValue) {
                conf = scope.conf;
                if(conf == undefined){
                    return
                }
                // onChange
                /*if(!value || value == oldValue) {
                    if(conf.onChange) {
                        conf.onChange();
                    }
                }*/
                getPagination();

                // 
                if(conf.pagesLength) {
                    // 
                    conf.pagesLength = parseInt(conf.pagesLength, 10);

                    if(!conf.pagesLength) {
                        conf.pagesLength = defaultPagesLength;
                    }
                    // 
                    if(conf.pagesLength % 2 === 0) {
                        conf.pagesLength += 1;
                    }
                } else {
                    conf.pagesLength = defaultPagesLength
                }

                // 
                if(!conf.perPageOptions){
                    conf.perPageOptions = defaultPagesLength;
                }
            });


            //timeoutpromise
            // var timer = $timeout(
            //     function() {
            //         conf = scope.conf;
            //         if(conf != undefined){
            //             $timeout.cancel( timer );
            //         }
            //     },
            //     100
            // );
            // 
            var defaultPagesLength = 10;

            // 
            var defaultPerPageOptions = [10, 15, 20, 30, 50];

            // 
            var defaultPerPage = 15;

            // pageList
            function getPagination(newValue, oldValue) {
                 if(conf.currentPage) {
                     conf.currentPage = parseInt(scope.conf.currentPage, 10);
                 }
                if(!conf.currentPage) {
                    conf.currentPage = 1;
                }
                // conf.totalItems
                if(conf.totalItems) {
                    conf.totalItems = parseInt(conf.totalItems, 10);
                }

                // conf.totalItems
                if(!conf.totalItems) {
                    conf.totalItems = 0;
                    return;
                }

                // conf.itemsPerPage
                if(conf.itemsPerPage) {
                    conf.itemsPerPage = parseInt(conf.itemsPerPage, 10);
                }
                if(!conf.itemsPerPage) {
                    conf.itemsPerPage = defaultPerPage;
                }
                // numberOfPages
                conf.numberOfPages = Math.ceil(conf.totalItems/conf.itemsPerPage);

                // >0
                if(scope.conf.numberOfPages > 0 && scope.conf.currentPage > scope.conf.numberOfPages){
                    scope.conf.currentPage = scope.conf.numberOfPages;
                }

                // itemsPerPageperPageOptionsitemsPerPage
                var perPageOptionsLength = scope.conf.perPageOptions.length;

                // 
                var perPageOptionsStatus;
                for(var i = 0; i < perPageOptionsLength; i++){
                    if(conf.perPageOptions[i] == conf.itemsPerPage){
                        perPageOptionsStatus = true;
                    }
                }
                // itemsPerPageperPageOptionsitemsPerPage
                if(!perPageOptionsStatus){
                    conf.perPageOptions.push(conf.itemsPerPage);
                }

                // sort
                conf.perPageOptions.sort(function(a, b) {return a - b});

                // 
                scope.pageList = [];
                if(conf.numberOfPages <= conf.pagesLength){
                    // 
                    for(i =1; i <= conf.numberOfPages; i++){
                        scope.pageList.push(i);
                    }
                }else{
                    // 1....2....3....
                    // 
                    var offset = (conf.pagesLength - 1) / 2;
                    if(conf.currentPage <= offset){
                        // ...
                        for(i = 1; i <= offset + 1; i++){
                            scope.pageList.push(i);
                        }
                        scope.pageList.push('...');
                        scope.pageList.push(conf.numberOfPages);
                    }else if(conf.currentPage > conf.numberOfPages - offset){
                        scope.pageList.push(1);
                        scope.pageList.push('...');
                        for(i = offset + 1; i >= 1; i--){
                            scope.pageList.push(conf.numberOfPages - i);
                        }
                        scope.pageList.push(conf.numberOfPages);
                    }else{
                        // ...
                        scope.pageList.push(1);
                        scope.pageList.push('...');

                        for(i = Math.ceil(offset / 2) ; i >= 1; i--){
                            scope.pageList.push(conf.currentPage - i);
                        }
                        scope.pageList.push(conf.currentPage);
                        for(i = 1; i <= offset / 2; i++){
                            scope.pageList.push(conf.currentPage + i);
                        }

                        scope.pageList.push('...');
                        scope.pageList.push(conf.numberOfPages);
                    }
                }
                scope.$parent.conf = conf;
            }

            // prevPage
            scope.prevPage = function() {
                if(conf.currentPage > 1){
                    conf.currentPage -= 1;
                }
                scope.conf.currentPage = conf.currentPage;
                getPagination();
                if(conf.onChange) {
                    conf.onChange();
                }
            };
            //
            scope.prevIndex = function () {
                if (scope.conf.currentPage > 1) {
                    scope.conf.currentPage = 1;
                    getPagination();
                    if (conf.onChange) {
                        conf.onChange();
                    }
                }

            };
            //
            scope.nextBack = function () {
                if (scope.conf.currentPage < conf.numberOfPages) {
                    scope.conf.currentPage = conf.numberOfPages ;
                    getPagination();
                    if (conf.onChange) {
                        conf.onChange();
                    }
                }

            };
            // nextPage
            scope.nextPage = function() {
                if(conf.currentPage < conf.numberOfPages){
                    conf.currentPage += 1;
                }
                scope.conf.currentPage = conf.currentPage;
                getPagination();
                if(conf.onChange) {
                    conf.onChange();
                }
            };

            // 
            scope.changeCurrentPage = function(item) {

                if(item == '...'){
                    return;
                }else{
                    conf.currentPage = item;
                    scope.conf.currentPage = item;
                    getPagination();
                    // conf.onChange()
                    if(conf.onChange) {
                        conf.onChange();
                    }
                }
            };

            // 
            scope.changeItemsPerPage = function() {
                // 1
                conf.currentPage = 1;
                getPagination();
                // conf.onChange()
                if(conf.onChange) {
                    conf.onChange();
                }
            };

            // 
            scope.jumpToPage = function() {
                var num = scope.jumpPageNum;
                if(num.match(/\d+/)) {
                    num = parseInt(num, 10);

                    if(num && num != conf.currentPage) {
                        if(num > conf.numberOfPages) {
                            num = conf.numberOfPages;
                        }
                        // 
                        conf.currentPage = num;
                        scope.conf.currentPage = num;
                        getPagination();
                        // conf.onChange()
                        if(conf.onChange) {
                            conf.onChange();
                        }
                        scope.jumpPageNum = '';
                    }
                }else{
                    showWarnMsg(toastr,"");
                }

            };

            scope.jumpPageKeyUp = function(e) {
                var keycode = window.event ? e.keyCode :e.which;

                if(keycode == 13) {
                    scope.jumpToPage();
                }
            };
        }
    };
}]);

function formatJson(json, options) {
    var reg = null,
        formatted = '',
        pad = 0,
        PADDING = '    '; // one can also use '\t' or a different number of spaces

    // optional settings
    options = options || {};
    // remove newline where '{' or '[' follows ':'
    options.newlineAfterColonIfBeforeBraceOrBracket = (options.newlineAfterColonIfBeforeBraceOrBracket === true) ? true : false;
    // use a space after a colon
    options.spaceAfterColon = (options.spaceAfterColon === false) ? false : true;

    // begin formatting...
    if (typeof json !== 'string') {
        // make sure we start with the JSON as a string
        json = JSON.stringify(json);
    } else {
        // is already a string, so parse and re-stringify in order to remove extra whitespace
        json = JSON.parse(json);
        json = JSON.stringify(json);
    }

    // add newline before and after curly braces
    reg = /([\{\}])/g;
    json = json.replace(reg, '\r\n$1\r\n');

    // add newline before and after square brackets
    reg = /([\[\]])/g;
    json = json.replace(reg, '\r\n$1\r\n');

    // add newline after comma
    reg = /(\,)/g;
    json = json.replace(reg, '$1\r\n');

    // remove multiple newlines
    reg = /(\r\n\r\n)/g;
    json = json.replace(reg, '\r\n');

    // remove newlines before commas
    reg = /\r\n\,/g;
    json = json.replace(reg, ',');

    // optional formatting...
    if (!options.newlineAfterColonIfBeforeBraceOrBracket) {
        reg = /\:\r\n\{/g;
        json = json.replace(reg, ':{');
        reg = /\:\r\n\[/g;
        json = json.replace(reg, ':[');
    }
    if (options.spaceAfterColon) {
        reg = /\:/g;
        json = json.replace(reg, ':');
    }

    $.each(json.split('\r\n'), function(index, node) {
        var i = 0,
            indent = 0,
            padding = '';

        if (node.match(/\{$/) || node.match(/\[$/)) {
            indent = 1;
        } else if (node.match(/\}/) || node.match(/\]/)) {
            if (pad !== 0) {
                pad -= 1;
            }
        } else {
            indent = 0;
        }

        for (i = 0; i < pad; i++) {
            padding += PADDING;
        }

        formatted += padding + node + '\r\n';
        pad += indent;
    });

    return formatted;
};



