        //
        $scope.debugInterface = function(testUrl,event){
            var pattern = new RegExp("http://(.*).fahaicc.com/fhfk/(.*)\?authCode=(.*)");
            if(!pattern.test(testUrl)){//url
                if(testUrl==null){
                    testUrl = '';
                }
                var resultStr = "url"+testUrl+"   \n";
                    resultStr += "urlhttp://api.fahaicc.com/fhfk/xxxxx?authCode=xxxxx";
                $scope.debugResult = resultStr;
            }else{//url
                $(event.target).attr('disabled',true);
                var testStr = "\n- - - - - - - - - - - Debug Interface Begin - - - - - - - - - - - - - \n";
                    testStr += "\n"+testUrl+"\n";
                $scope.debugResult = testStr;
                setTimeout(function(){
                    interfaceService.debugInterfaceUrl(testUrl).then(function(data){
                        testStr += "\n\n";
                        if(data.data=='1'){
                            testStr += "url\n";
                        }else{
                            testStr += formatJson(data.data);
                        }
                        testStr += "\n- - - - - - - - - - - Debug Interface End - - - - - - - - - - - - - \n";
                        $scope.debugResult = testStr;
                        $(event.target).attr('disabled',false);
                    },function(data){
                        commonService.goLoginPage(data.errorCode);
                    })
                },500);


            }
        }

        //
        var timerHandler;
        $scope.interfaceDebug = function(){
            var debugUrl = $scope.interface.testURL;
            var interfaceURL = $scope.interface.interfaceURL;

            var url="";
            var str="" ;
            if(debugUrl ){
                str = "<span style='color: black'>url:"+debugUrl+"</span><br/>";
                url = debugUrl;
            }else if(interfaceURL){
                url = interfaceURL;
                str = "<span style='color: black'>url:"+interfaceURL+"</span><br/>";
            }else{
                str="<span style='color: red'>urlurl</span>";
                $scope.debugResult=str;
                return ;
            }
            $scope.debugResult=str;
            str+="<span style='color: green'>";
            $scope.debugResult=str;

            var second=20;
            timerHandler=$interval(function(){
                if(second = 20){
                    //
                    interfaceService.debugUrl(url).then(function (data) {
                        if(data.errorCode == '0'){
                            $interval.cancel(timerHandler);
                            var resultJosn = data.data;
                            str+="</span><br/>";
                            str+="<span style='color: #9428ff'>"+formatJson(resultJosn)+"</span>";
                            $scope.debugResult=str;
                        }else{
                            $interval.cancel(timerHandler);
                            str+="</span><br/>";
                            str+="<span style='color: red'>!</span>";
                            $scope.debugResult=str;
                        }
                    },function (data) {
                        commonService.goLoginPage(data.errorCode);
                    });
                }else{
                    if(second<=0){
                         $interval.cancel(timerHandler);
                         str+="</span><br/>";
                         str+="<span style='color: red'>,!</span>";
                          $scope.debugResult=str;
                    }
                    str+=".";
                    $scope.debugResult=str;
                }
                second--;
            },500);

        };


